.recipe{
    display: flex;
    align-items: center;
    /* align-items: center; */
    margin-bottom: 2rem;
}
.recipe a{
    display: flex;
    color: black;
    text-decoration: none;
}
.recipe a:hover{
    color: rgb(63, 184, 63);
}
.recipe img{
    border: 5px solid rgb(228, 228, 228);
    background-color: rgb(228, 228, 228);
    height: 10rem;
    width: 10rem;
    margin-right: 3rem;
    border-radius: 2rem;
}

@media screen and (max-width: 900px) {
    .recipe h1{
        font-size: 1.5rem;
    }
    .recipe img{
        height: 8rem;
        width: 8rem;
    }
}