:root{
    --logo-size: 2;
}
@media screen and (max-width: 600px){
  .home_container{
    --logo-size: 3;
  }
}
@media screen and (max-width: 300px){
  .home_container{
    --logo-size: 2;
  }
}
.logo {
  display: flex;
  align-items: center;
}
.logo .icon {
  color: rgb(63, 184, 63);
  background-color: white;
  padding: calc(0.2rem * var(--logo-size));
  border-radius: 50%;
  font-size: calc(0.8rem * var(--logo-size));
  margin-right: calc(0.2rem * var(--logo-size));
}
.logo .title{
    font-size: calc(1rem * var(--logo-size));
    color: white;
}
