@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
*{
    padding: 0;
    margin: 0;
    font-family: Quicksand;
    color: rgb(61, 85, 12);
    box-sizing: border-box;
}

body{
    overflow: none;
}
a{
    text-decoration: none;
}
ul{
    list-style: none;
}
nav{
    background-color: rgb(63, 184, 63);
    padding: 2rem 0;
    margin-bottom: 3rem;
}
.container{
    width: 90%;
    margin: 0 auto;
}
.nav_container{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

nav .search{
    width: 50%;
}

@media screen and (max-width: 900px) {
    .nav_container{
        width: 70%;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    nav .search{
        width: 100%;
    }
    nav a{
        display: inline-block;
        margin-bottom: 2rem;
    }
}
.home {
  height: 100vh;
  background-image: url(/Foodtionary/static/media/WBC_7095.0837e5b0.jpg);
  background-size: cover;
}
.glass {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  height: 100vh;
  background: rgba(63, 184, 63, 0);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

.home_container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  /* background-color: red; */
  width: 50%;
  min-width: 300px;
}
.home_container h2 {
  color: rgb(255, 255, 255);
  text-align: center;
  margin-bottom: 2rem;
  -webkit-filter: drop-shadow(0 0 5px rgb(128, 255, 128));
          filter: drop-shadow(0 0 5px rgb(128, 255, 128));
}
.search {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-filter: drop-shadow(5px 5px 10px rgba(48, 48, 48, 0.521));
          filter: drop-shadow(5px 5px 10px rgba(48, 48, 48, 0.521));
  width: 100%;
}
.search input {
  box-sizing: border-box;
  width: 70%;
  font-size: 1.5rem;
  padding: 1rem 2rem;
  border: none;
  border-radius: 1rem 0 0 1rem;
}
.search button {
  width: 25%;
  cursor: pointer;
  padding: 1rem;
  border: none;
  border-radius: 0 1rem 1rem 0;
  font-size: 1.5rem;
  font-weight: bold;
  background-color: rgb(63, 184, 63);
  color: white;
}
@media screen and (max-width: 1290px) {
  .search button {
    width: 40%;
  }
}
@media screen and (max-width: 900px) {
  .search {
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }
  .search input {
    border-radius: 1rem;
    font-size: 1rem;
    width: 100%;
    margin-bottom: 1rem;
  }
  .search button {
    width: 50%;
    border-radius: 1rem;
    font-size: 1rem;
  }
}

:root{
    --logo-size: 2;
}
@media screen and (max-width: 600px){
  .home_container{
    --logo-size: 3;
  }
}
@media screen and (max-width: 300px){
  .home_container{
    --logo-size: 2;
  }
}
.logo {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.logo .icon {
  color: rgb(63, 184, 63);
  background-color: white;
  padding: calc(0.2rem * 2);
  padding: calc(0.2rem * var(--logo-size));
  border-radius: 50%;
  font-size: calc(0.8rem * 2);
  font-size: calc(0.8rem * var(--logo-size));
  margin-right: calc(0.2rem * 2);
  margin-right: calc(0.2rem * var(--logo-size));
}
.logo .title{
    font-size: calc(1rem * 2);
    font-size: calc(1rem * var(--logo-size));
    color: white;
}


.recipe_name{
    margin-bottom: 2rem;
    color: rgb(63, 184, 63);
}

.ingredients_container{
    padding: 2rem;
    width: 60%;
}
.desktop_img{
    max-width: 100%;
    width: 100%;
    margin-bottom: 2rem;
}
.mobile_img{
    display: none;
}
.ingredients_wrapper{
    padding: 1rem 2rem;
    border-left: 5px solid rgba(63, 184, 63, 0.459);
    width: 50%;
}
.ingredients{
    margin-top: 1rem;
    font-size: 1.3rem;
    list-style: square ;
    list-style-position: inside;
}
.ingredients li{
    margin-bottom: .5rem;
}
.ingredients li::marker{
    color: rgb(63, 184, 63);
}
.flex{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}


.extra{
    display: -webkit-flex;
    display: flex;
    margin-top: 3rem;
}

.extra > div{
    padding: 0.5rem 1rem;
    margin-right: 1rem;
    border-left: 5px solid rgba(63, 184, 63, 0.5);
    background-color: rgba(63, 184, 63, 0.075);
}

table{
    border-collapse: collapse;
}

table,td,th{
    border: 1px solid rgb(63, 184, 63);
}
tr { display: block; float: left; width: -webkit-fit-content; width: -moz-fit-content; width: fit-content;}
th, td { display: block; padding: .8rem 2.5rem; }

@media screen and (max-width: 1200px) {
    .ingredients_container{
        width: 90%;
    }
}
@media screen and (max-width: 900px) {
    .ingredients_container{
        width: 90%;
    }
    .mobile_img{
        display: block;
        margin-bottom: 4rem;
    }
    .desktop_img{
        display: none;
    }
    .flex{
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
    }
    .ingredients_wrapper{
        width: 100%;
        margin-bottom: 3rem;
    }
    .idk{
        width: 100%;
    }
    table,tbody{
        width: 100%;
    }
    tr{
        width: 50%;
    }
    th, td { display: block; padding: .8rem 1rem; }
  }
.recipe{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    /* align-items: center; */
    margin-bottom: 2rem;
}
.recipe a{
    display: -webkit-flex;
    display: flex;
    color: black;
    text-decoration: none;
}
.recipe a:hover{
    color: rgb(63, 184, 63);
}
.recipe img{
    border: 5px solid rgb(228, 228, 228);
    background-color: rgb(228, 228, 228);
    height: 10rem;
    width: 10rem;
    margin-right: 3rem;
    border-radius: 2rem;
}

@media screen and (max-width: 900px) {
    .recipe h1{
        font-size: 1.5rem;
    }
    .recipe img{
        height: 8rem;
        width: 8rem;
    }
}
