.home {
  height: 100vh;
  background-image: url(WBC_7095.jpg);
  background-size: cover;
}
.glass {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background: rgba(63, 184, 63, 0);
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(5px);
}

.home_container {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* background-color: red; */
  width: 50%;
  min-width: 300px;
}
.home_container h2 {
  color: rgb(255, 255, 255);
  text-align: center;
  margin-bottom: 2rem;
  filter: drop-shadow(0 0 5px rgb(128, 255, 128));
}
.search {
  display: flex;
  justify-content: center;
  filter: drop-shadow(5px 5px 10px rgba(48, 48, 48, 0.521));
  width: 100%;
}
.search input {
  box-sizing: border-box;
  width: 70%;
  font-size: 1.5rem;
  padding: 1rem 2rem;
  border: none;
  border-radius: 1rem 0 0 1rem;
}
.search button {
  width: 25%;
  cursor: pointer;
  padding: 1rem;
  border: none;
  border-radius: 0 1rem 1rem 0;
  font-size: 1.5rem;
  font-weight: bold;
  background-color: rgb(63, 184, 63);
  color: white;
}
@media screen and (max-width: 1290px) {
  .search button {
    width: 40%;
  }
}
@media screen and (max-width: 900px) {
  .search {
    flex-direction: column;
    align-items: center;
  }
  .search input {
    border-radius: 1rem;
    font-size: 1rem;
    width: 100%;
    margin-bottom: 1rem;
  }
  .search button {
    width: 50%;
    border-radius: 1rem;
    font-size: 1rem;
  }
}
