
.recipe_name{
    margin-bottom: 2rem;
    color: rgb(63, 184, 63);
}

.ingredients_container{
    padding: 2rem;
    width: 60%;
}
.desktop_img{
    max-width: 100%;
    width: 100%;
    margin-bottom: 2rem;
}
.mobile_img{
    display: none;
}
.ingredients_wrapper{
    padding: 1rem 2rem;
    border-left: 5px solid rgba(63, 184, 63, 0.459);
    width: 50%;
}
.ingredients{
    margin-top: 1rem;
    font-size: 1.3rem;
    list-style: square ;
    list-style-position: inside;
}
.ingredients li{
    margin-bottom: .5rem;
}
.ingredients li::marker{
    color: rgb(63, 184, 63);
}
.flex{
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}


.extra{
    display: flex;
    margin-top: 3rem;
}

.extra > div{
    padding: 0.5rem 1rem;
    margin-right: 1rem;
    border-left: 5px solid rgba(63, 184, 63, 0.5);
    background-color: rgba(63, 184, 63, 0.075);
}

table{
    border-collapse: collapse;
}

table,td,th{
    border: 1px solid rgb(63, 184, 63);
}
tr { display: block; float: left; width: fit-content;}
th, td { display: block; padding: .8rem 2.5rem; }

@media screen and (max-width: 1200px) {
    .ingredients_container{
        width: 90%;
    }
}
@media screen and (max-width: 900px) {
    .ingredients_container{
        width: 90%;
    }
    .mobile_img{
        display: block;
        margin-bottom: 4rem;
    }
    .desktop_img{
        display: none;
    }
    .flex{
        flex-direction: column;
        align-items: center;
    }
    .ingredients_wrapper{
        width: 100%;
        margin-bottom: 3rem;
    }
    .idk{
        width: 100%;
    }
    table,tbody{
        width: 100%;
    }
    tr{
        width: 50%;
    }
    th, td { display: block; padding: .8rem 1rem; }
  }